<template>
	<div class="bill-info-block fx-shadow-block">
		<van-collapse v-model="activeNames" :border="false">
			<van-collapse-item name="1" :disabled="collapseDisabled">
				<div slot="title" class="info-title">
					<span class="info-title-name"></span>
					<stateText class="stateTag fl-r" :billState="data.billState"></stateText>
					<span class="info-title-weightState" v-show="data.weightState === 1"></span>
				</div>
				<van-tabs v-model="tabActive" animated>
					<van-tab title="">
						<van-cell-group>
							<van-cell class="info-item" title="" :value="data.shopDTO && data.shopDTO.name"/>
							<van-cell class="info-item" title="" :value="data.rdcDTO && data.rdcDTO.name"/>
							<van-cell class="info-item" title="" :value="data.sysbillCode"/>
							<van-cell
								class="info-item"
								title=""
								:value="formatterDate(data.businessDate)"
								:is-link="!isDisabled && $fxSysAuth('distributionInBill.modifyWorkDate')"
								@click="onBusDateClick"
							/>
							<van-cell
								class="info-item"
								title=""
								:value="formatterDate(data.arriveDate)"
								:is-link="!isDisabled"
								@click="onEmitClick('on-arriveDate-click')"
							/>
							<van-cell
								class="info-item"
								title=""
								:value="data.businessTypeDTO && data.businessTypeDTO.name"
							/>
							<van-pro-field
								v-model="data.billMemo"
								class="info-item"
								label=""
								:placeholder="isDisabled ? '' : ''"
								input-align="right"
								:disabled="isDisabled"
							/>
						</van-cell-group>
					</van-tab>
					<van-tab title="">
						<van-cell-group>
							<van-cell class="info-item" title="" :value="data.billEmp"/>
							<van-cell class="info-item" title="" :value="formatterDate(data.billDate)"/>
							<van-cell class="info-item" title="" :value="data.checkEmp"/>
							<van-cell class="info-item" title="" :value="formatterDate(data.checkDate)"/>
							<van-cell class="info-item" title="" :value="formatterDate(data.orderDate)"/>
							<van-cell class="info-item" title="" :value="formatterDate(data.deliverDate)"/>
							<van-pro-field
								v-model="data.manualCode"
								class="info-item"
								label=""
								:placeholder="isDisabled ? '' : ''"
								input-align="right"
								maxlength="50"
								:disabled="isDisabled"
							/>
						</van-cell-group>
					</van-tab>
				</van-tabs>
			</van-collapse-item>
		</van-collapse>
	</div>
</template>

<script>
import stateText from '@/components/common/StateText.vue'
export default {
	name: 'billInfo',
	components: {
		stateText
	},
	props: {
		data: {
			type: Object,
			default: () => {
				return {}
			}
		},
		isDisabled: Boolean,
		collapseDisabled: Boolean,
		checkAllItemFlag: Boolean
	},
	data () {
		return {
			activeNames: ['1'],
			tabActive: 0
		}
	},
	computed: {
		formatterDate (date) {
			return (date) => {
				if (typeof date === 'number') {
					return this.$fxUtils.setDateTime(new Date(date), '')
				} else {
					return ''
				}
			}
		}
	},
	watch: {
		checkAllItemFlag: {
			handler (val) {
				if (val) {
					this.activeNames = ['0']
				} else {
					this.activeNames = ['1']
				}
			},
			immediate: true
		}
	},
	methods: {
		onEmitClick (type) {
			if (this.isDisabled) {
				return false
			}
			this.$emit(type)
		},
		closeCollapse () {
			this.activeNames = []
		},
		onBusDateClick () {
			if (!this.$fxSysAuth('distributionInBill.modifyWorkDate')) {
				this.$toast({
					message: '!',
					position: 'top'
				})
				return false
			}
			this.onEmitClick('on-businessDate-click')
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~$assets/stylus/varsty.styl'
.bill-info-block {
	width:340px
	margin:10px auto
	padding:5px
	text-align:left
	.info-title {
		.info-title-name {
			font-size:12px
		}
		.stateTag {
			font-size:12px
			margin-right:10px
		}
		.info-title-weightState {
			margin-left:20px
			color:$fxRed
			font-size:16px
		}
	}
	.info-item {
		font-size:12px !important
		padding:5px 0 !important
		.info-item-value {
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}
}
</style>
<style lang="stylus">
.bill-info-block {
	.van-collapse-item__content {
		padding-top: 5px;
		padding-bottom: 5px;
		.van-tabs--line {
			padding-top: 24px !important
		}
		.van-tabs__wrap {
			height: 24px !important
			.van-ellipsis {
				height:24px
				line-height:24px
				font-size:12px
			}
			&.van-hairline--top-bottom::after {
				border-width: 0 !important;
			}
		}
	}
}
</style>
